import {
  Stack,
  Box,
  Typography,
  Badge,
  IconButton,
  useScrollTrigger,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { NotificationsNone, Save } from "@mui/icons-material";
import Notification from "../pages/Notification";

const Navbar = ({ token, onLogout, isCreator }) => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isNotificationOpen, setIsNotificationOpen] = useState(false);
  const [notificationData, setNotificationData] = useState([]);
  const [shownav, setShowNav] = useState(true);
  const trigger = useScrollTrigger();

  const isLoggedIn = !!token;

  const handleLogout = () => {
    onLogout();
  };

  const handleMenuToggle = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleNotificationToggle = () => {
    setIsNotificationOpen(!isNotificationOpen);

    setNotificationData((prevData) =>
      prevData.map((notification) => ({ ...notification, read: true }))
    );
  };

  useEffect(() => {
    const fetchNotificatioData = async () => {
      try {
        const response = await fetch(
          "http://127.0.0.1:8000/api/notifications/",
          {
            headers: {
              Authorization: `Bearer ${token}`,
              "Content-Type": "application/json",
            },
          }
        );

        const data = await response.json();
        setNotificationData(data);
      } catch (error) {
        console.error("Error fetching notification data:", error);
      }
    };

    if (token) {
      fetchNotificatioData();
    }

    const handleScroll = () => {
      setShowNav(!trigger);
    };

    window.addEventListener("scroll", handleScroll);

    return () => window.removeEventListener("scroll", handleScroll);
  }, [token, trigger]);

  const MenuStyles = isMenuOpen
    ? { left: "0" }
    : { left: { xs: "-150%", sm: "0", md: "0", lg: "0" } };

  return (
    <Stack
      sx={{
        transition: "opacity 0.5s ease-in-out",
        opacity: shownav ? 1 : 0,
        pointerEvents: shownav ? "auto" : "none",
      }}
    >
      <Stack
        flexDirection="row"
        justifyContent="space-around"
        backgroundColor="#000"
        p={1}
        mt={0}
        width="100%"
        position="fixed"
        zIndex={1}
      >
        <Box>
        <Link to="/">
          <Typography
            sx={{
              fontSize: {
                lg: "20px",
                md: "20px",
                sm: "17px",
                xm: "17px",
                opacity: 0.8,
              },
            }}
            textTransform="uppercase"
            color="#fff"
          >
            Fashion
            <span style={{ color: "red" }}>Fusion</span>
          </Typography>
          </Link>
        </Box>

        <Box display="flex" gap={2} className="NavLinks" sx={{ ...MenuStyles }}>
          <Link to="/">
            {" "}
            <li>Home</li>{" "}
          </Link>
        

          {isLoggedIn ? (
            <>
              <li onClick={handleLogout}>Logout</li>
              {isCreator ? (
                <Link to="/dashboard">
                  <li>Dashboard</li>{" "}
                </Link>
              ) : (
                <Link to="/creator/account/create">
                  <li>Create Account</li>
                </Link>
              )}
            </>
          ) : (
            <>
              <Link to="/register">
                <li>Register</li>
              </Link>
              <Link to="/login">
                <li>Login</li>
              </Link>
            </>
          )}
        </Box>

        <Box onClick={handleMenuToggle}>
          <MenuIcon
            sx={{
              display: { lg: "none", md: "none", sm: "block", xm: "block" },
              color: "#fff",
            }}
          />
        </Box>
      </Stack>

      {isLoggedIn ? (
        <>
          <Stack
            position="fixed"
            top="40%"
            right={24}
            p="0 7px"
            borderLeft="1px solid #000"
            gap={2}
            zIndex="1"
            className="floatingLinks"
          >
            <Box>
              <IconButton color="primary" onClick={handleNotificationToggle}>
                <Badge
                  badgeContent={
                    notificationData.length > 0
                      ? notificationData.filter(
                          (notification) => !notification.read
                        ).length
                      : 0
                  }
                  color="error"
                >
                  <NotificationsNone
                    sx={{
                      backgroundColor: "#000",
                      color: "#fff",
                      padding: "7px",
                      fontSize: "32px",
                    }}
                  />
                </Badge>
              </IconButton>

              <Notification
                isOpen={isNotificationOpen}
                onClose={handleNotificationToggle}
                notificationData={notificationData}
                token={token}
              />
            </Box>

            <Box>
              <Link to="/fashion/saved">
                <Badge badgeContent={2} color="error">
                  <Save
                    sx={{
                      backgroundColor: "#000",
                      color: "#fff",
                      padding: "7px",
                      fontSize: "32px",
                      ml: 1
                    }}
                  />
                </Badge>
              </Link>
            </Box>
          </Stack>
        </>
      ) : (
        ""
      )}
    </Stack>
  );
};

export default Navbar;
