import { Box, Stack, Typography, IconButton } from "@mui/material";
import { Facebook, Twitter, Instagram } from "@mui/icons-material";

const Footer = () => {
  return (
    <Stack
      flexDirection="row"
      justifyContent="space-around"
      mt={20}
      backgroundColor="#D4D4D4"
      padding="30px 0"
    >
      <Box textAlign="right">
        <Typography variant="bpdy6" color="text.secondary">
          Develoved With Passion by  : <br />
          <span
            style={{
              color: "red",
              marginTop: "3px",
              opacity: "0.7",
              fontSize: "12px",
              marginLeft: "7px",
            }}
          >
            {" "}
            Maxwell Reuben{" "}
          </span>
        </Typography>
      </Box>

      <Stack direction="row" spacing={2} justifyContent="center">
        <IconButton aria-label="Facebook" href="" target="_blank">
          <Facebook />
        </IconButton>
        <IconButton aria-label="Twitter" href="" target="_blank">
          <Twitter />
        </IconButton>
        <IconButton aria-label="Instagram" href="" target="_blank">
          <Instagram />
        </IconButton>
      </Stack>
    </Stack>
  );
};

export default Footer;
