import {
  Stack,
  Divider,
  Typography,
  CircularProgress,
  Card,
  IconButton,
  Alert
} from "@mui/material";
import React, { useState, useEffect } from "react";
import GroupIcon from "@mui/icons-material/Group";
import ReviewsIcon from "@mui/icons-material/Reviews";
import FormatListBulletedIcon from "@mui/icons-material/FormatListBulleted";

const Overview = ({ creator }) => {
  const [creatorData, setCreatorData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  


  const currentCreator = creator[0]?.id;

  useEffect(() => {
    const fetchCreator = async () => {
      try {
        // Fetch fashion data
        const fashionResponse = await fetch(
          "http://localhost:8000/api/fashions/"
        );
        const fashionData = await fashionResponse.json();

        // Fetch following data
        const followingResponse = await fetch(
          "http://localhost:8000/api/creator_following/"
        );
        const followingData = await followingResponse.json();

        // Merge creator details with following data
        const mergedCreator = {
          ...creator,
          followers: followingData.filter(
            (item) => item.creator === currentCreator
          ),
        };

        // Fetch review ratings data
        const reviewRatingsResponse = await fetch(
          "http://localhost:8000/api/creator-ratings/"
        );
        const reviewRatingsData = await reviewRatingsResponse.json();

        // Merge creator details with review ratings data
        const creatorWithRatings = {
          ...mergedCreator,
          ratings: reviewRatingsData.filter(
            (item) => item.creator === currentCreator
          ),
        };

        const creatorWithFashions = {
          ...creatorWithRatings,
          fashions: fashionData.filter(
            (item) => item.creator === currentCreator
          ),
        };

        // Set the merged creator data
        setCreatorData(creatorWithFashions);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching creator details:", error);
        setError('Error fetching creator details');
        setLoading(false);
      }
    };

    fetchCreator();
  }, [creator, currentCreator]);

  return (
    <Stack>
      <Typography
        className="Heading"
        variant="h6"
        color="#000"
        textTransform="uppercase"
        mt={1}
      >
        OverView
      </Typography>
      <Divider sx={{ mt: 5, backgroundColor: "white" }} />
      <Stack
        direction="row"
        mt={5}
        justifyContent="center"
        alignItems="center"
        width="100%"
      >
        {loading &&  
        <Stack
          direction="row"
          mt={5}
          justifyContent="center"
          alignItems="center"
          width="100%"
        >
          <CircularProgress />
        </Stack>}
        {error && (
         <Stack
         direction="row"
         mt={5}
         justifyContent="center"
         alignItems="center"
         width="100%"
       >
         <Alert severity="error">{error}</Alert>
       </Stack>
      )}
      </Stack>
      {creatorData && (
        <Stack
          sx={{
            flexDirection: {
              lg: "row",
              md: "row",
              flexWrap: "wrap",
              justifyContent: "space-around",
              gap: 7,
            },
          }}
        >
          <Card
            sx={{
              textAlign: "center",
              alignContent: "center",
              padding: 4,
              borderRadius: 2,
              maxWidth: { lg: 250, md: 250 },
            }}
            className="OverviewCard"
          >
            <IconButton>
              <FormatListBulletedIcon
                sx={{
                  fontSize: 50,
                  backgroundColor: "#D4D4D4",
                  p: 2,
                  borderRadius: "50px",
                }}
              />
            </IconButton>

            <Typography variant="h4" fontWeight="600" mt={1}>
              [ {creatorData?.fashions?.length} ]
            </Typography>
            <Typography color="text.secondary" mt={2} variant="body1">
              Fashions
            </Typography>
          </Card>

          <Card
            sx={{
              textAlign: "center",
              alignContent: "center",
              padding: 4,
              borderRadius: 2,
              maxWidth: { lg: 250, md: 250 },
            }}
            className="OverviewCard"
          >
            <IconButton>
              <GroupIcon
                sx={{
                  fontSize: 50,
                  backgroundColor: "#D4D4D4",
                  p: 2,
                  borderRadius: "50px",
                }}
              />
            </IconButton>

            <Typography variant="h4" fontWeight="600" mt={1}>
              [ {creatorData?.followers?.length} ]
            </Typography>
            <Typography color="text.secondary" mt={2} variant="body1">
              followers
            </Typography>
          </Card>

          <Card
            sx={{
              textAlign: "center",
              alignContent: "center",
              padding: 4,
              borderRadius: 2,
              maxWidth: { lg: 250, md: 250 },
            }}
            className="OverviewCard"
          >
            <IconButton>
              <ReviewsIcon
                sx={{
                  fontSize: 50,
                  backgroundColor: "#D4D4D4",
                  p: 2,
                  borderRadius: "50px",
                }}
              />
            </IconButton>

            <Typography variant="h4" fontWeight="600" mt={1}>
              [ {creatorData?.ratings?.length} ]
            </Typography>
            <Typography color="text.secondary" mt={2} variant="body1">
              Reviews
            </Typography>
          </Card>
        </Stack>
      )}
    </Stack>
  );
};

export default Overview;
