import React, { useState, useEffect } from "react";
import {
  Box,
  Typography,
  Card,
  Divider,
  Grid,
  Avatar,
  IconButton,
  CardContent,
  CardMedia,
  Rating,
  Stack,
  Button,
  CircularProgress,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  List,
  ListItem,
} from "@mui/material";
import { Facebook, Twitter, Instagram } from "@mui/icons-material";
import { useParams, Link } from "react-router-dom";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { customSlick01 } from "../utils/SlickSettings";
import KeyboardBackspaceIcon from "@mui/icons-material/KeyboardBackspace";
import RateReviewIcon from '@mui/icons-material/RateReview';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';

const CreatorDetails = ({ token }) => {
  const { id } = useParams();
  const [currentUser, setCurrentUser] = useState(null);
  const [creator, setCreator] = useState(null);
  const [fashions, setFashions] = useState([]);

  const [loading, setLoading] = useState(true);
  const [openReviewDialog, setOpenReviewDialog] = useState(false);
  const [openReviewRead, setOpenReviewRead] = useState(false);
  const [reviewText, setReviewText] = useState("");
  const [rating, setRating] = useState(0);
  const [reviewUsers, setReviewUsers] = useState({});
  const [isFollowing, setIsFollowing] = useState(false);

  //Fetch current user
  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const response = await fetch("http://localhost:8000/api/users/user/", {
          headers: {
            Authorization: `Bearer ${token}`,
            "Content-Type": "application/json",
          },
        });

        if (response.ok) {
          const data = await response.json();

          setCurrentUser(data);
        }
      } catch (error) {
        console.error("Error fetching user data:", error);
      }
    };

    fetchUserData();
  }, [token]);

  useEffect(() => {
    const fetchCreator = async () => {
      try {
        // Fetch fashion data
        const fashionResponse = await fetch(
          "http://localhost:8000/api/fashions/"
        );
        const fashionData = await fashionResponse.json();
        setFashions(fashionData);

        // Fetch creator details
        const responseCreator = await fetch(
          `http://localhost:8000/api/creators/creator/${id}`
        );
        if (!responseCreator.ok) {
          throw new Error("Failed to fetch creator details");
        }
        const creatorData = await responseCreator.json();

        // Fetch following data
        const followingResponse = await fetch(
          "http://localhost:8000/api/creator_following/"
        );
        const followingData = await followingResponse.json();

        const currentUserFollowing = followingData.find(
          (item) =>
            item.creator === creatorData?.id &&
            item.user === currentUser?.[0]?.id
        );
        setIsFollowing(!!currentUserFollowing);

        // Merge creator details with following data
        const mergedCreator = {
          ...creatorData,
          followers: followingData.filter(
            (item) => item.creator === creatorData?.id
          ),
        };

        // Fetch review ratings data
        const reviewRatingsResponse = await fetch(
          "http://localhost:8000/api/creator-ratings/"
        );
        const reviewRatingsData = await reviewRatingsResponse.json();

        // Merge creator details with review ratings data
        const creatorWithRatings = {
          ...mergedCreator,
          ratings: reviewRatingsData.filter(
            (item) => item.creator === creatorData.id
          ),
        };

        // Calculate average rating
        let totalStars = 0;
        creatorWithRatings.ratings.forEach((rating) => {
          totalStars += rating.rating;
        });
        const averageRating = totalStars / creatorWithRatings.ratings.length;

        // Merge creator details with average rating
        const creatorWithAverageRating = {
          ...creatorWithRatings,
          rating: averageRating, // Set average rating
        };

        // Set the merged creator data
        setCreator(creatorWithAverageRating);
        setLoading(false);

        // Fetch and set review users
        const users = {};
        for (const review of creatorWithAverageRating.ratings) {
          if (!users[review.user]) {
            const userResponse = await fetch(
              `http://localhost:8000/api/users/user/${review.user}`
            );
            if (userResponse.ok) {
              const userData = await userResponse.json();
              users[review.user] = userData.username;
            }
          }
        }
        setReviewUsers(users);
      } catch (error) {
        console.error("Error fetching creator details:", error);
        setLoading(false);
      }
    };

    fetchCreator();
  }, [id, currentUser]);

  const handleOpenReviewDialog = () => {
    setOpenReviewDialog(true);
  };

  const handleCloseReviewDialog = () => {
    setOpenReviewDialog(false);
    setReviewText('');
    setRating('');
  };

  const handleOpenReviewRead = () => {
    setOpenReviewRead(true);
  };

  const handleCloseReviewRead = () => {
    setOpenReviewRead(false);
  };

  const handleReviewTextChange = (event) => {
    setReviewText(event.target.value);
  };

  const handleRatingChange = (value) => {
    setRating(value);
  };

  const handleReviewSubmit = async () => {
    try {
      const response = await fetch(
        `http://localhost:8000/api/creator/review/create/`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify({
            creator: id,
            rating: rating,
            review_text: reviewText,
          }),
        }
      );
      if (!response.ok) {
        throw new Error("Failed to submit review");
      }
      const data = await response.json();
      window.location.reload()
      console.log("Review submitted successfully:", data);

      handleCloseReviewDialog();
    } catch (error) {
      console.error("Error submitting review:", error);
    }
  };

  const handleFollow = async () => {
    try {
      if (isFollowing) {
        // Unfollow creator
        const response = await fetch(
          `http://localhost:8000/api/creator_following_update/${creator.id}/`,
          {
            method: "DELETE",
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        if (!response.ok) {
          throw new Error("Failed to unfollow creator");
        }
        console.log("Unfollowed creator successfully");
        setIsFollowing(false);
        window.location.reload()
      } else {
        // Follow creator
        const response = await fetch(
          `http://localhost:8000/api/create_creator_following/`,
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${token}`,
            },
            body: JSON.stringify({
              creator: creator.id,
            }),
          }
        );
        if (!response.ok) {
          throw new Error("Failed to follow creator");
        }
        console.log("Followed creator successfully");
        setIsFollowing(true);
        window.location.reload()
      }
    } catch (error) {
      console.error("Error following/unfollowing creator:", error);
    }
  };

  //filter creator fashions
  const creatorFashions = fashions.filter(
    (fashion) => fashion.creator === creator?.id
  );

  return (
    <section>
      <Typography
        className="Heading"
        mt={1}
        variant="h6"
        color="#000"
        textTransform="uppercase"
      >
        Fashionista Details
      </Typography>
      <Divider sx={{ mt: "17px", backgroundColor: "white" }} />

      <Stack
        direction="row"
        mt={5}
        justifyContent="center"
        alignItems="center"
        width="100%"
      >
        {loading && <CircularProgress />}
      </Stack>

      {creator && (
        <Grid container justifyContent="center" spacing={3}>
          <Grid item xs={12} md={8}>
            <Stack
              direction={{ xs: "column", md: "row" }}
              alignItems="center"
              spacing={2}
              mt={3}
            >
              <Avatar
                alt={creator.full_name}
                src={`http://localhost:8000${creator.profile_picture}`}
                sx={{ width: 100, height: 100 }}
              />
              <div>
                <Typography variant="h5">{creator.full_name}</Typography>
                <Typography variant="body1" color="text.secondary">
                  {creator.description}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  {creator.specialty}
                </Typography>
                <Rating value={creator.rating} precision={0.1} readOnly sx={{fontSize: 14}} />
                <Stack mt={2}>
                  <Stack flexDirection="row" gap={3}>
                    <IconButton
                      sx={{
                       backgroundColor: "#D4D4D4",
                       p: '3px 30px',
                       borderRadius: "20px",
                      }}
                      onClick={handleOpenReviewRead}
                    >
                    
                    <Typography fontSize='12px' color='text.secondary'>  
                    {creator.ratings.length} 
                    </Typography>
                    <Typography fontSize='12px' color='text.secondary' ml={1}>  
                    Reviews
                    </Typography>

                    </IconButton>

                    <IconButton
                      sx={{
                       backgroundColor: "#D4D4D4",
                       p: '3px 30px',
                       borderRadius: "20px",
                      }}
                     
                    >
                    
                    <Typography fontSize='12px' color='text.secondary'>  
                    {creator.followers.length}
                    </Typography>
                    <Typography fontSize='12px' color='text.secondary' ml={1}>  
                    followers{" "}
                    </Typography>
                    </IconButton>
                  </Stack>
                  {token && (
                    <Stack flexDirection="row" gap={5} mt={3}>
                      <IconButton
                      sx={{
                       backgroundColor: "#D4D4D4",
                       p: '3px 30px',
                       borderRadius: "20px",
                      }}
                        onClick={handleOpenReviewDialog}
                      >
                       <RateReviewIcon sx={{fontSize: 12}}/>
                      <Typography fontSize='12px' color='text.secondary' ml={1}>  
                    Rate
                    </Typography>
                      </IconButton>

                      <IconButton
                      sx={{
                       backgroundColor: "#D4D4D4",
                       p: '3px 30px',
                       borderRadius: "20px",
                      }}
                      onClick={handleFollow}
                      >
                       <ArrowUpwardIcon sx={{fontSize: 15}}/>
                      <Typography fontSize='12px' color='text.secondary' ml={1}>  
                      {isFollowing ? "Unfollow" : "Follow"}
                    </Typography>
                      </IconButton>
                    </Stack>
                  )}
                </Stack>
              </div>
            </Stack>

            <Dialog open={openReviewRead} onClose={handleCloseReviewRead}>
              <DialogTitle>All Reviews</DialogTitle>
              <DialogContent>
                <List>
                  {creator.ratings.map((review, index) => (
                    <ListItem key={index}>
                      <Card sx={{ padding: 2, textAlign: "left" }}>
                       
                        <Typography mt={1} color='text.secondary' fontSize={13}>{review.review_text}</Typography>
                        <Typography color="text.secondary" fontSize={12} opacity={0.7}>
                         by  {reviewUsers[review.user]}
                        </Typography>
                        <Divider sx={{ backgroundColor: "white", mt: 1 }} />
                      </Card>
                    </ListItem>
                  ))}
                </List>
              </DialogContent>
              <DialogActions>
                <Button onClick={handleCloseReviewRead}>Close</Button>
              </DialogActions>
            </Dialog>
          </Grid>

          <Dialog open={openReviewDialog} onClose={handleCloseReviewDialog}>
            <DialogTitle>Write a Review</DialogTitle>
            <DialogContent>
              <Rating
                name="rating"
                value={rating}
                onChange={(event, newValue) => handleRatingChange(newValue)}
              />
              <TextField
                autoFocus
                margin="dense"
                id="reviewText"
                label="Review"
                type="text"
                fullWidth
                value={reviewText}
                onChange={handleReviewTextChange}
              />
            </DialogContent>
            <DialogActions>
              <Button onClick={handleCloseReviewDialog}>Cancel</Button>
              <Button onClick={handleReviewSubmit}>Submit</Button>
            </DialogActions>
          </Dialog>

          <Grid item xs={12} md={4}>
            <Stack
              direction="row"
              spacing={2}
              mt={{ xs: 2, md: 0, lg: 5 }}
              justifyContent="left"
            >
              <IconButton
                aria-label="Facebook"
                href={creator.facebook}
                target="_blank"
              >
                <Facebook />
              </IconButton>
              <IconButton
                aria-label="Twitter"
                href={creator.twitter}
                target="_blank"
              >
                <Twitter />
              </IconButton>
              <IconButton
                aria-label="Instagram"
                href={creator.instagram}
                target="_blank"
              >
                <Instagram />
              </IconButton>
            </Stack>
          </Grid>
        </Grid>
      )}

      <Divider sx={{ backgroundColor: "white", mt: 4 }} />
      <Typography
        className="Heading"
        variant="h6"
        color="#000"
        textTransform="uppercase"
        mt={7}
      >
        Stuffs
      </Typography>
      <Stack>
        <Slider
          {...customSlick01}
          style={{ textAlign: "left", marginTop: "50px" }}
        >
          {creatorFashions.map((fashion, index) => (
            <Box
              sx={{
                maxWidth: { lg: 250, md: 250 },
              }}
              className="FashionCard"
            >
              <Link to={`/fashion/${fashion.id}`}>
                <Box>
                <CardMedia
                  component="img"
                  height='300px'
                  image={`http://localhost:8000${fashion.image}`}
                  alt={fashion.name}
                  sx={{ objectFit: "cover" }}
                  className="FashionCardImg"
                ></CardMedia>
                </Box>
                <CardContent>
                  <Typography
                    gutterBottom
                    variant="body6"
                    component="div"
                    color="text.secondary"
                  >
                    {fashion.name}
                  </Typography>
                  <Box
                    mt={1}
                    display="inline-flex"
                    gap={1}
                    sx={{ cursor: "pointer", textAlign: "center" }}
                  >
                    <Typography
                      sx={{
                        transform: "rotate(180deg)",
                        opacity: 0.8,
                      }}
                    >
                      <KeyboardBackspaceIcon
                        sx={{
                          fontSize: 30,
                          backgroundColor: "#D4D4D4",
                          borderRadius: "50px",
                          color: "#000",
                          padding: 1,
                        }}
                      />
                    </Typography>
                    <Typography
                      color="text.secondary"
                      mt={0.7}
                      variant="body6"
                      padding={1}
                    >
                      Details
                    </Typography>
                  </Box>
                </CardContent>
              </Link>
            </Box>
          ))}
        </Slider>
      </Stack>
    </section>
  );
};

export default CreatorDetails;
