import { Box, Button, Stack, Typography, CardMedia } from "@mui/material";
import React, { useState, useEffect } from "react";
import KeyboardBackspaceIcon from "@mui/icons-material/KeyboardBackspace";
import animatedfashionIcon from "../assets/imgs/animatedfashionIcon.gif";

const About = ({ abouts }) => {
  const [typedContent, setTypedContent] = useState("");
  const [isTyping, setIsTyping] = useState(true);
  const [index, setIndex] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      if (isTyping && abouts && abouts.length > 0) {
        const content = abouts[index]?.content;
        if (content) {
          setTypedContent(content.substring(0, typedContent.length + 1));
          if (typedContent === content) {
            setIsTyping(false);
          }
        }
      }
    }, 100);

    return () => clearInterval(interval);
  }, [typedContent, isTyping, index, abouts]);

  const showNextAboutContent = () => {
    setTypedContent("");
    setIsTyping(true);
    setIndex((index + 1) % abouts.length);
  };

  return (
    <section>
      <Stack
        textAlign="left"
        sx={{
          flexDirection: { lg: "row", md: "row", sm: "column", xm: "column" },
          justifyContent: { lg: "space-around" },
          gap: 5,
        }}
      >
        <Box>
          <CardMedia
            component="img"
            height="auto"
            width={150}
            image={animatedfashionIcon}
            alt="About"
            sx={{ objectFit: "cover", loading: "lazy", borderRadius: "5px" }}
          ></CardMedia>
        </Box>

        <Box sx={{ width: { lg: "50%", md: "100%", sm: "100%", xm: "100%" } }}>
          <Typography className="Heading" color="text.secondary" mb={2}>
            Who we are
          </Typography>
          <Typography
            variant="body6"
            gutterBottom
            textTransform="capitalize"
            fontFamily="cursive"
            lineHeight={2}
          >
            {typedContent}
          </Typography>
          <Typography mt={2}>
            <Button
              variant="contained"
              sx={{ backgroundColor: "#000" }}
              onClick={showNextAboutContent}
            >
              <a href="#services" style={{ color: "#fff" }}>
                Our Services
              </a>
              <span
                style={{
                  marginLeft: "12px",
                  transform: "rotate(180deg)",
                  marginTop: "-8px",
                }}
              >
                <KeyboardBackspaceIcon />
              </span>
            </Button>
          </Typography>
        </Box>
      </Stack>
    </section>
  );
};

export default About;
