import React, { useState, useEffect } from 'react';
import { CircularProgress, Paper, Stack, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography, Divider, Avatar, IconButton } from '@mui/material';
import { Link } from "react-router-dom";
import DeleteIcon from '@mui/icons-material/Delete';

const Creations = ({ creators, token }) => {
    const [fashions, setFashions] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [creatorId, setCreatorId] = useState(null);

    useEffect(() => {
        if (creators.length > 0) {
            setCreatorId(creators[0].id);
        }
    }, [creators]);

    useEffect(() => {
        const fetchData = async () => {
            try {
                if (!creatorId) return;

                setLoading(true);
                setError(null);

                const fashionResponse = await fetch("http://localhost:8000/api/fashions/");
                const fashionData = await fashionResponse.json();

                const filteredFashions = fashionData.filter(fashion => fashion.creator === creatorId);

                const commentsResponse = await fetch("http://localhost:8000/api/comments/");
                const commentsData = await commentsResponse.json();

                const likesResponse = await fetch("http://localhost:8000/api/likes/");
                const likesData = await likesResponse.json();

                const reviewRatingsResponse = await fetch("http://localhost:8000/api/review-ratings/");
                const reviewRatingsData = await reviewRatingsResponse.json();

                const mergedFashionData = filteredFashions.map(fashion => {
                    const fashionComments = commentsData.filter(comment => comment.fashion === fashion.id);
                    const fashionLikes = likesData.filter(like => like.fashion === fashion.id);
                    const fashionReviewRatings = reviewRatingsData.filter(rating => rating.fashion === fashion.id);

                    return {
                        ...fashion,
                        comments: fashionComments,
                        likes: fashionLikes,
                        reviewRatings: fashionReviewRatings
                    };
                });

                setFashions(mergedFashionData);
                setLoading(false);
            } catch (error) {
                console.error("Error fetching data:", error);
                setError("Error fetching data. Please try again later.");
                setLoading(false);
            }
        };

        fetchData();
    }, [creatorId]);

    const handleDelete = async (id) => {
        try {
          const response = await fetch(
            `http://localhost:8000/api/fashion_update/${id}/`,
            {
              method: "DELETE",
              headers: {
                Authorization: `Bearer ${token}`,
                "Content-Type": "application/json",
              },
            }
          );
    
          if (!response.ok) {
            throw new Error("Failed to delete fashion");
          }
    window.location.reload()
       
        } catch (error) {
          console.error("Error deleting fashion:", error);
          setError("Error deleting fashion. Please try again.");
        }
      };
   

    return (
        <Stack spacing={2}>
            <Typography variant="h6" color="primary">My Stuffs</Typography>
            <Divider />
            <Stack direction="row" justifyContent="center" alignItems="center" width="100%" mt={50}>
            {loading && <CircularProgress />}
            {error && <Typography color="error">{error}</Typography>}
            </Stack>
            {fashions.length > 0 &&
                <TableContainer component={Paper}>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell>Image</TableCell>
                                <TableCell>Name</TableCell>
                                <TableCell>Total Comments</TableCell>
                                <TableCell>Total Reviews</TableCell>
                                <TableCell>Delete</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {fashions.map(fashion => (
                                <TableRow key={fashion.id}>
                                    <TableCell> <Avatar alt="User Avatar" src={`http://localhost:8000${fashion.image}`} sx={{ width: 60, height: 60, borderRadius: '5px'}} /></TableCell>
                                    <TableCell>  <Link to={`/fashion/${fashion.id}`}>{fashion.name} </Link></TableCell>
                                   
                                    <TableCell>{fashion.comments.length}</TableCell>
                                    <TableCell>{fashion.reviewRatings.length}</TableCell>

                                    <TableCell>
                                        <IconButton>
                     < DeleteIcon  onClick={() => handleDelete(fashion.id)}/>
                     </IconButton>
                    </TableCell>
                                </TableRow>
                                
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            }
        </Stack>
    );
}

export default Creations;
