import { Stack, Box } from "@mui/material";
import Siderbar from "./Siderbar";
import Content from "./Content";
import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

const Feeds = ({token}) => {
    const [selectedItem, setSelectedItem] = useState("Overview");
    const [creators, setCreators] = useState([]);
    const navigate = useNavigate();
    const [error, setError] = useState(null);
    const [loading, setLoading] = useState(true);
  

    useEffect(() => {
      const fetchCreatorData = async () => {
        try {
          const response = await fetch("http://localhost:8000/api/creators/creator/", {
            headers: {
              Authorization: `Bearer ${token}`,
              "Content-Type": "application/json",
            },
          });
  
          if (!response.ok) {
            if (response.status === 404) {
              navigate("/creator/account/create/");
            } else {
              throw new Error(`HTTP error! Status: ${response.status}`);
            }
          }
  
          const data = await response.json();
          setCreators(data);
        } catch (error) {
          console.error("Error fetching creator data:", error);
          setError('Error fetching data!');
          setLoading(false);
        }
      };
  
      fetchCreatorData();
    }, [token, navigate]);

  return (
    <Stack sx={{ flexDirection: { sx: "column", md: "row" } }}>
      <Box sx={{ borderRight: "1px solid red", padding: "15px" }}>
        <Siderbar selectedItem={selectedItem} setSelectedItem={setSelectedItem} />
      </Box>

      <Box sx={{ margin: 2, overflowY: "auto", height: "90vh", flex: 2 }}>
        <Content selectedItem={selectedItem} creators={creators} setCreators={setCreators} token={token} error={error} loading={loading}/>
      </Box>
    </Stack>
  )
}

export default Feeds
