import CreateFashion from "./CreateFashion"
import Creations from "./Creations"
import Overview from "./Overview"
import Profile from "./Profile"

const Content = ({selectedItem, setCreators, creators, token, loading, error}) => {
  
  return (
    <section>
  
  {selectedItem === "Overview" && (
      <Overview creator={creators} setCreators={setCreators} token={token}/>
      )}

{selectedItem === "Profile" && (
      <Profile creators={creators} setCreators={setCreators} token={token} error={error} loading={loading}/>
      )}

{selectedItem === "Add Fashion" && (
      <CreateFashion creators={creators} setCreators={setCreators} token={token}/>
      )}

{selectedItem === "My Stuffs" && (
      <Creations creators={creators} setCreators={setCreators} token={token}  error={error} loading={loading}/>
      )}
      
      




    </section>
  )
}

export default Content

