import {
  Stack,
  Typography,
  Select,
  MenuItem,
  Box,
  CardContent,
  CardMedia,
  Pagination,
  Divider,
  CircularProgress,
  Alert,
} from "@mui/material";
import React, { useState } from "react";
import NavigateNextIcon from "@mui/icons-material/NavigateNext";
import KeyboardBackspaceIcon from "@mui/icons-material/KeyboardBackspace";
import Slider from "react-slick";
import { Link } from "react-router-dom";


const Services = ({ categories, fashions, loading, error }) => {
  const [selectedCategory, setSelectedCategory] = useState("ALL");

  const [page, setPage] = useState(1);

  const CustomArrow = ({ onClick, icon }) => (
    <button
      onClick={onClick}
      style={{
        position: "absolute",
        left: "14%",
        top: "50%",
        border: "none",
        outline: "none",
        zIndex: "1",
        background: "transparent",
        cursor: "pointer",
      }}
    >
      {icon}
    </button>
  );

  const customPrevArrow = (
    <CustomArrow
      icon={
        <NavigateNextIcon
          style={{
            color: "gray",
            width: "100%",
            marginRight: "350px",
            opacity: 0.7,
          }}
        />
      }
    />
  );

  const customNextArrow = (
    <CustomArrow
      icon={
        <NavigateNextIcon
          style={{
            color: "gray",
            width: "100%",
            transform: "rotate(180deg)",
            opacity: 0.7,
          }}
        />
      }
    />
  );

  var settings = {
    infinite: true,
    speed: 1000,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 5000,
    nextArrow: customNextArrow,
    prevArrow: customPrevArrow,
  };

  const handleChange = (event) => {
    setSelectedCategory(event.target.value);
    setPage(1);
  };

  const filteredFashions =
    selectedCategory === "ALL"
      ? fashions
      : fashions.filter((fashion) => fashion.category === selectedCategory);

  const startIndex = (page - 1) * 6;
  const endIndex = page * 6;

  const paginatedFashions = filteredFashions.slice(startIndex, endIndex);

  const handlePageChange = (event, value) => {
    setPage(value);
  };

  return (
    <section style={{ overflowX: "hidden" }} id="services">
      <Typography
        className="Heading"
        variant="h6"
        color="#000"
        textTransform="uppercase"
      >
        Fashions
      </Typography>
      <Divider sx={{ mt: 5, backgroundColor: "white" }} />
      <Box width="25%">
        <Select
          label="Select Category"
          value={selectedCategory}
          onChange={handleChange}
          sx={{ minWidth: 120, color: "#000", backgroundColor: "white" }}
        >
          <MenuItem value="ALL">ALL</MenuItem>
          {categories.map((category, index) => (
            <MenuItem key={index} value={category.id}>
              {category.name}
            </MenuItem>
          ))}
        </Select>
      </Box>
      <Divider sx={{ backgroundColor: "white" }} />
      {loading && (
        <Stack
          direction="row"
          mt={5}
          justifyContent="center"
          alignItems="center"
          width="100%"
        >
          <CircularProgress />
        </Stack>
      )}
      {error && (
         <Stack
         direction="row"
         mt={5}
         justifyContent="center"
         alignItems="center"
         width="100%"
       >
         <Alert severity="error">{error}</Alert>
       </Stack>
      )}
      <Stack
        sx={{
          flexDirection: { lg: "row", md: "row", sm: "column", xm: "column" },
          justifyContent: "flex-start",
          gap: 3,
          flexWrap: "wrap",
          mt: 3,
        }}
      >
        {paginatedFashions.map((fashion, index) => (
          <Box
            sx={{
              maxWidth: { lg: 250, md: 250 },
             
            }}
            className="FashionCard"
          >
            <Box>
              <Slider {...settings}>
                <CardMedia
                  component="img"
                  height='300px'
                  image={`http://localhost:8000${fashion.image}`}
                  alt={fashion.name}
                  sx={{ objectFit: "cover" }}
                  className="FashionCardImg"
                ></CardMedia>

                <CardMedia
                  component="img"
                  height='300px'
                  image={`http://localhost:8000${fashion.thumbnail1}`}
                  alt={fashion.name}
                  sx={{ objectFit: "cover" }}
                  className="FashionCardImg"
                ></CardMedia>

                <CardMedia
                  component="img"
                  height='300px'
                  image={`http://localhost:8000${fashion.thumbnail2}`}
                  alt={fashion.name}
                  sx={{ objectFit: "cover" }}
                  className="FashionCardImg"
                ></CardMedia>

                  <CardMedia
                  component="img"
                  height='300px'
                  image={`http://localhost:8000${fashion.thumbnail3}`}
                  alt={fashion.name}
                  sx={{ objectFit: "cover" }}
                  className="FashionCardImg"
                ></CardMedia>

              </Slider>

              <CardContent>
                <Typography
                  gutterBottom
                  variant="body6"
                  component="div"
                  color="text.secondary"
                 
                >
                  {fashion.name}
                </Typography>
                <Link to={`/fashion/${fashion.id}`}>
                  <Box
                    mt={1}
                    display="inline-flex"
                    gap={1}
                    sx={{ cursor: "pointer", textAlign: "center" }}
                  >
                    <Typography
                      sx={{
                        transform: "rotate(180deg)",
                        opacity: 0.8,
                      }}
                    >
                      <KeyboardBackspaceIcon
                        sx={{
                          fontSize: 30,
                          backgroundColor: "#D4D4D4",
                          borderRadius: "50px",
                          color: "#000",
                          padding: 1,
                        }}
                      />
                    </Typography>
                    <Typography
                      color="text.secondary"
                      mt={0.7}
                      variant="body3"
                      padding={1}
                    >
                      Details
                    </Typography>
                  </Box>
                </Link>
              </CardContent>
            </Box>
          </Box>
        ))}
      </Stack>

      <Box mt={3} display="flex" justifyContent="center">
        <Pagination
          count={Math.ceil(filteredFashions.length / 6)}
          page={page}
          onChange={handlePageChange}
          color="primary"
        />
      </Box>
    </section>
  );
};

export default Services;
