import React, { useState, useEffect, useRef } from "react";
import { useParams, Link } from "react-router-dom";
import {
  Typography,
  Divider,
  Stack,
  IconButton,
  CardContent,
  CardMedia,
  Box,
  CircularProgress,
  Rating,
  List,
  ListItem,
  ListItemText,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Alert,
  AlertTitle,
  TextField,
  Button,
} from "@mui/material";
import {  CommentOutlined } from "@mui/icons-material";
import AddchartIcon from '@mui/icons-material/Addchart';
import KeyboardBackspaceIcon from "@mui/icons-material/KeyboardBackspace";
import PersonIcon from '@mui/icons-material/Person';
import RateReviewIcon from '@mui/icons-material/RateReview';
import ShareIcon from "@mui/icons-material/Share";
import { WhatsApp, Twitter } from "@mui/icons-material";
import EmailIcon from "@mui/icons-material/Email";
import NavigateNextIcon from '@mui/icons-material/NavigateNext';

const FashionDetails = ({ token }) => {
  const { id } = useParams();
  const [fashionDetails, setFashionDetails] = useState(null);
  const [fashions, setFashions] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [showComments, setShowComments] = useState(false);
  const [commentText, setCommentText] = useState("");
  const [reviewRating, setReviewRating] = useState(0);
  const [reviewText, setReviewText] = useState("");
  const [showReviewForm, setShowReviewForm] = useState(false);
  const [reviewUsers, setReviewUsers] = useState({});
  const [formLoading, setFormLoading] = useState(false);
  const [formError, setFormError] = useState(null);
  const [currentUser, setCurrentUser] = useState(null);
  const [isSaved, setIsSaved] = useState(false);
  const [selectedImage, setSelectedImage] = useState(null);
  const fashionDetailRef = useRef(null);
  const [isShareDialogOpen, setIsShareDialogOpen] = useState(false);
  const [showadditionalinfo, setShowadditionalinfo] = useState(false);
  const [showorderinfo, setShoworderinfo] = useState(false);

  // Fetch current user
  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const response = await fetch("http://localhost:8000/api/users/user/", {
          headers: {
            Authorization: `Bearer ${token}`,
            "Content-Type": "application/json",
          },
        });

        if (response.ok) {
          const data = await response.json();

          setCurrentUser(data);
        }
      } catch (error) {
        console.error("Error fetching user data:", error);
      }
    };

    fetchUserData();
  }, [token]);

  useEffect(() => {
    const fetchFashionDetails = async () => {
      try {
        // Fetch fashion details
        const fashionResponse = await fetch(
          `http://localhost:8000/api/fashion-details/${id}/`
        );
        if (!fashionResponse.ok) {
          throw new Error("Failed to fetch fashion details");
        }
        const fashionData = await fashionResponse.json();

        // Fetch comments data
        const commentsResponse = await fetch(
          "http://localhost:8000/api/comments/"
        );
        const commentsData = await commentsResponse.json();

        // Fetch likes data
        const likesResponse = await fetch("http://localhost:8000/api/likes/");
        const likesData = await likesResponse.json();

        // Fetch review ratings data
        const reviewRatingsResponse = await fetch(
          "http://localhost:8000/api/review-ratings/"
        );
        const reviewRatingsData = await reviewRatingsResponse.json();

        const relatedFashionsResponse = await fetch(
          "http://localhost:8000/api/fashions/"
        );
        const relatedFashionsData = await relatedFashionsResponse.json();
        setFashions(relatedFashionsData);

        // Fetch favorites
        const favoriteResponse = await fetch(
          "http://localhost:8000/api/favorites/"
        );
        const favoriteData = await favoriteResponse.json();

        const currentUserFavorite = favoriteData.find(
          (item) =>
            item.fashion === fashionData?.id &&
            item.user === currentUser?.[0]?.id
        );
        setIsSaved(!!currentUserFavorite);

        setFashionDetails({
          ...fashionData,
          comments: commentsData.filter(
            (comment) => comment.fashion === fashionData.id
          ),
          likes: likesData.filter((like) => like.fashion === fashionData.id),
          reviewRatings: reviewRatingsData.filter(
            (rating) => rating.fashion === fashionData.id
          ),
        });

        // Fetch and set review users
        const users = {};
        for (const review of reviewRatingsData) {
          if (!users[review.user]) {
            const userResponse = await fetch(
              `http://localhost:8000/api/users/user/${review.user}`
            );
            if (userResponse.ok) {
              const userData = await userResponse.json();
              users[review.user] = userData.username;
            }
          }
        }
        setReviewUsers(users);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching fashion details:", error);
        setError("Error fetching fashion details. Please try again.");
        setLoading(false);
      }
    };

    fetchFashionDetails();
  }, [id, currentUser]);

  const handleCommentClick = () => {
    setShowComments(true);
  };

  const handleCloseComments = () => {
    setShowComments(false);
  };

  const handleReviewSubmit = async () => {
    
    setFormLoading(true);
    setFormError(null);
    
    try {
      const response = await fetch("http://localhost:8000/api/review/create/", {
        method: "POST",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          rating: reviewRating,
          review_text: reviewText,
          fashion: id,
        }),
      });
      if (!response.ok) {
        throw new Error("Failed to submit review");
      }

      window.location.reload();
    } catch (error) {
      console.error("Error submitting review:", error);
      setFormError("Error submitting review. Please try again.");
    } finally {
      setFormLoading(false);
    }
  };

  // handle Save
  const handleSaveFashion = async () => {
    try {
      const response = await fetch(`http://localhost:8000/api/add_favorite/`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({
          fashion: fashionDetails.id,
        }),
      });
      if (!response.ok) {
        throw new Error("Failed to save fashion");
      }
      const data = await response.json();
      console.log("fashion saved successfully:", data);
      window.location.reload();

    } catch (error) {
      console.error("Error submitting review:", error);
    }
  };

  const handleCommentSubmit = async () => {
    // Submit comment
    try {
      const response = await fetch(
        "http://localhost:8000/api/comment/create/",
        {
          method: "POST",
          headers: {
            Authorization: `Bearer ${token}`,
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ text: commentText, fashion: id }),
        }
      );
      if (!response.ok) {
        throw new Error("Failed to submit comment");
      }

      window.location.reload();
    } catch (error) {
      console.error("Error submitting comment:", error);
      setFormError("Error submitting comment. Please try again.");
    }
  };

  const handleShareClick = () => {
    setIsShareDialogOpen(true);
  };

  const handleShareClose = () => {
    setIsShareDialogOpen(false);
  };

  const handleShareFashion = (platform) => {
    let shareUrl = "";
    switch (platform) {
      case "twitter":
        shareUrl = `https://twitter.com/intent/tweet?url=${encodeURIComponent(
          window.location.href
        )}`;
        break;
      case "whatsapp":
        window.open(
          `https://wa.me/?text=${encodeURIComponent(window.location.href)}`,
          "_blank"
        );
        break;
      case "gmail":
        window.open(
          `mailto:?subject=Check out this fashion&body=Hey, I found this amazing fashion and wanted to share it with you: ${window.location.href}`
        );
        break;
      default:
        break;
    }
    window.open(shareUrl, "_blank");
  };

  //filter related fashions
  const relatedFashions = fashions?.filter(
    (fashion) => fashion.category === fashionDetails?.category
    
  );


  const scrollToFashionDetail = () => {
    fashionDetailRef.current.scrollIntoView({ behavior: "smooth" });
  };

  const showadditionalinfoStyles = showadditionalinfo
    ? { display: "block" }
    : { display: "none" };

  const showorderinfoStyles = showorderinfo
    ? { display: "block" }
    : { display: "none" };

  const handleThumbnailClick = (image) => {
    setSelectedImage(image);
  };

  const handleAdditionalInfoToggle = () => {
    setShowadditionalinfo(!showadditionalinfo);
  };

  const handleOrderInfoToggle = () => {
    setShoworderinfo(!showorderinfo);
  };

  return (
    <section>
      <div ref={fashionDetailRef}>
        <Typography
          className="Heading"
          variant="h6"
          color="#000"
          textTransform="uppercase"
          mt={1}
        >
          Fashion Details
        </Typography>
      </div>
      <Divider sx={{ mt: 5, backgroundColor: "white" }} />
      <Stack
        direction="row"
        mt={5}
        justifyContent="center"
        alignItems="center"
        width="100%"
      >
        {loading && <CircularProgress />}

        {error && (
          <Alert severity="error">
            <AlertTitle>Error</AlertTitle>
            {error}
          </Alert>
        )}
      </Stack>

      {fashionDetails && (
        <Stack sx={{ flexDirection: { sx: "column", md: "row" } }}>
          {/* thumbnails images */}
          <Stack
            direction="row"
            gap={3}
            sx={{
              overflowY: "auto",
              height: { xm: "auto", md: "95%" },
              flexDirection: { md: "column" },
              mb: 3,
            }}
          >
             <CardMedia
              component="img"
              maxWidth={20}
              height="auto"
              image={`http://localhost:8000${fashionDetails.image}`}
              alt={fashionDetails.name}
              sx={{ objectFit: "cover", maxWidth: 70, borderRadius: 1 }}
              onClick={() => handleThumbnailClick(`http://localhost:8000${fashionDetails.image}`)}
            ></CardMedia>
            <CardMedia
              component="img"
              maxWidth={20}
              height="auto"
              image={`http://localhost:8000${fashionDetails.thumbnail1}`}
              alt={fashionDetails.name}
              sx={{ objectFit: "cover", maxWidth: 70, borderRadius: 1 }}
              onClick={() => handleThumbnailClick(`http://localhost:8000${fashionDetails.thumbnail1}`)}
            ></CardMedia>

            <CardMedia
              component="img"
              maxWidth={20}
              height="auto"
              image={`http://localhost:8000${fashionDetails.thumbnail2}`}
              alt={fashionDetails.name}
              sx={{ objectFit: "cover", maxWidth: 70, borderRadius: 1 }}
              onClick={() => handleThumbnailClick(`http://localhost:8000${fashionDetails.thumbnail2}`)}
            ></CardMedia>

            <CardMedia
              component="img"
              maxWidth={20}
              height="auto"
              image={`http://localhost:8000${fashionDetails.thumbnail3}`}
              alt={fashionDetails.name}
              sx={{ objectFit: "cover", maxWidth: 70, borderRadius: 1 }}
              onClick={() => handleThumbnailClick(`http://localhost:8000${fashionDetails.thumbnail3}`)}
            ></CardMedia>
          </Stack>

          <Box
            //   main image area
            sx={{
              overflowY: "auto",
              height: "90vh",
              flex: 2,
              ml: { lg: 4, md: 4 },
              maxWidth: { lg: 400, md: 400 },
              objectFit: "cover",
              mb: 5,
            }}
          >
            <CardMedia
              component="img"
              height="auto"
              image={selectedImage || `http://localhost:8000${fashionDetails.image}`}
              alt={fashionDetails.name}
              sx={{
                borderRadius: 1,
                maxWidth: "100%",
              }}
            ></CardMedia>
          </Box>

          <Box
            sx={{
              overflowY: "auto",
              height: "90vh",
              flex: 2,
              ml: { lg: 8, md: 6 },
              maxWidth: { lg: 450, md: 450 },
            }}
          >
            <Typography variant="h5" component="div" color="primary">
              {fashionDetails.name}
            </Typography>

            <Typography variant="subtitle1" color="text.secondary" gutterBottom>
              {fashionDetails.description}
            </Typography>
            <Box>
              <IconButton
                onClick={handleAdditionalInfoToggle}
                sx={{ color: "#000", fontSize: 15 }}
              >
                 Additional Information  <NavigateNextIcon sx={{ml: 4}}/>
              </IconButton>

              <Typography
                variant="subtitle1"
                color="text.secondary"
                gutterBottom
                sx={{ ...showadditionalinfoStyles }}
              >
                {fashionDetails.additinal_info}
              </Typography>
            </Box>
            <IconButton
              onClick={handleOrderInfoToggle}
              sx={{ color: "#000", fontSize: 15 }}
            >
              How can i get the Product  <NavigateNextIcon sx={{ml: 4}}/>
            </IconButton>
            <Typography
              variant="subtitle1"
              color="text.secondary"
              gutterBottom
              sx={{ ...showorderinfoStyles }}
            >
              {fashionDetails.order_info}
            </Typography>

            <Box mt={2}>
              <Rating
                name="rating"
                value={fashionDetails.reviewRatings[0]?.rating}
                readOnly
                sx={{fontSize: 15}}
              />
            </Box>

            <Stack direction="row" spacing={2} alignItems="center" mt={2}>
              {token && !isSaved && (
                <IconButton  sx={{
                  backgroundColor: "#D4D4D4",
                  p: "3px 30px",
                  borderRadius: "20px",
                }}>
                  <AddchartIcon sx={{ fontSize: 15 }} onClick={handleSaveFashion} />
                  <Typography color="text.secondary" fontSize="12px" ml={1}>
                  Save
                </Typography>
                </IconButton>
              )}

              <IconButton
                onClick={handleCommentClick}
                sx={{
                  backgroundColor: "#D4D4D4",
                  p: "3px 30px",
                  borderRadius: "20px",
                }}
              >
                <CommentOutlined sx={{ fontSize: 15 }} />
                <Typography color="text.secondary" fontSize="12px" ml={1}>
                  {fashionDetails.comments.length}
                </Typography>
              </IconButton>

              <IconButton
                onClick={handleShareClick}
                sx={{
                  backgroundColor: "#D4D4D4",
                  p: "3px 30px",
                  borderRadius: "20px",
                }}
              >
                <ShareIcon sx={{ fontSize: 15 }} />
                <Typography color="text.secondary" fontSize="12px" ml={1}>
                  Share
                </Typography>
              </IconButton>
            </Stack>

           

            <Dialog open={isShareDialogOpen} onClose={handleShareClose}>
              <DialogTitle>Share</DialogTitle>
              <DialogContent>
                <IconButton onClick={() => handleShareFashion("whatsapp")}>
                  <WhatsApp />
                </IconButton>
                <IconButton onClick={() => handleShareFashion("twitter")}>
                  <Twitter />
                </IconButton>
                <IconButton onClick={() => handleShareFashion("gmail")}>
                  <EmailIcon />
                </IconButton>
              </DialogContent>
            </Dialog>

            {/* Comments Dialog */}
            {showComments && fashionDetails && (
              <Dialog open={showComments} onClose={handleCloseComments}>
                <DialogTitle>Comments</DialogTitle>
                <DialogContent>
                  {formLoading && <CircularProgress />}
                  <Divider sx={{ backgroundColor: "white" }} />
                  <List>
                    {fashionDetails.comments.map((comment, index) => (
                      <ListItem key={index}>
                        <ListItemText
                          primary={comment.text}
                          secondary={`By ${
                            reviewUsers[comment.user] || "Anonymous"
                          }`}
                        />
                      </ListItem>
                    ))}
                  </List>

                  {token ? (
                    <TextField
                      autoFocus
                      margin="dense"
                      id="comment"
                      label="Add Comment"
                      fullWidth
                      value={commentText}
                      onChange={(e) => setCommentText(e.target.value)}
                    />
                  ) : (
                    <Typography variant="body2" color="error">
                      Please login to comment
                    </Typography>
                  )}
                </DialogContent>
                <DialogActions>
                  {token && (
                    <Button
                      disabled={formLoading}
                      onClick={handleCommentSubmit}
                      color="primary"
                    >
                      {formLoading ? "Submitting..." : "Submit"}
                    </Button>
                  )}
                  <Button onClick={handleCloseComments} color="secondary">
                    Cancel
                  </Button>
                </DialogActions>
              </Dialog>
            )}

            {/* Review Form */}
            {showReviewForm && fashionDetails && (
              <Dialog
                open={showReviewForm}
                onClose={() => setShowReviewForm(false)}
              >
                <DialogTitle>Write a Review</DialogTitle>
                <DialogContent>
                  <Divider sx={{ backgroundColor: "white" }} />
                  <Rating
                    name="reviewRating"
                    value={reviewRating}
                    onChange={(event, newValue) => setReviewRating(newValue)}
                    sx={{ mt: 2 }}
                  />
                  <Divider sx={{ mt: 2, backgroundColor: "white" }} />
                  <TextField
                    autoFocus
                    margin="dense"
                    id="reviewText"
                    label="Review"
                    fullWidth
                    multiline
                    value={reviewText}
                    onChange={(e) => setReviewText(e.target.value)}
                    sx={{ mt: 3 }}
                  />
                  {formError && <Alert severity="error">{formError}</Alert>}
                </DialogContent>
                <DialogActions>
                  <Button
                    disabled={formLoading}
                    onClick={handleReviewSubmit}
                    color="primary"
                  >
                    {formLoading ? "Submitting..." : "Submit"}
                  </Button>
                  <Button
                    disabled={formLoading}
                    onClick={() => setShowReviewForm(false)}
                    color="secondary"
                  >
                    Cancel
                  </Button>
                </DialogActions>
              </Dialog>
            )}

            <Box display="flex" gap={3} mt={3}>
              {/* Buttons for opening review forms */}
              {token && (
            
                  <IconButton
                   sx={{
                    backgroundColor: "#D4D4D4",
                    p: "3px 30px",
                    borderRadius: "20px",
                   }}
                    onClick={() => setShowReviewForm(true)}
                  >
                    <RateReviewIcon sx={{fontSize: 15}} />
                    <Typography fontSize='12px' color='text.secondary' ml={1}>  
                    {formLoading ? "Submitting Review..." : "Write a Review"}
                    </Typography>
                  
                  </IconButton>
                
              )}

           
                <Box>
                <Link to={`/creator/${fashionDetails.creator}`}>
                  <IconButton sx={{
                   backgroundColor: "#D4D4D4",
                   p: "3px 30px",
                   borderRadius: "20px",
                  }}>
                    <PersonIcon sx={{
                      fontSize: 15,
                    
                     }} />
                    <Typography fontSize='14px' color='text.secondary' ml={1}>Fashionista</Typography>
                  </IconButton>
                
                </Link>
                </Box>
            </Box>
          </Box>
        </Stack>
      )}
      <Divider sx={{ mt: 5, backgroundColor: "white" }} />
      <Stack mt={3}>
        <Typography
          className="Heading"
          variant="h6"
          color="#000"
          textTransform="uppercase"
          mt={1}
        >
          Related Fashions
        </Typography>
        <Stack
          sx={{
            flexDirection: { lg: "row", md: "row", sm: "column", xm: "column" },
            justifyContent: "flex-start",
            gap: 3,
            flexWrap: "wrap",
            mt: 4,
          }}
        >
          {relatedFashions?.slice(0, 8).map((fashion, index) => (
            <Box
              key={index}
              sx={{
                maxWidth: { lg: 250, md: 250 },
              }}
              className="FashionCard"
            >
              <Box>
              <CardMedia
                  component="img"
                  height='300px'
                  image={`http://localhost:8000${fashion.image}`}
                  alt={fashion.name}
                  sx={{ objectFit: "cover" }}
                  className="FashionCardImg"
                ></CardMedia>

                <CardContent>
                  <Typography
                    gutterBottom
                    variant="body6"
                    component="div"
                    color="text.secondary"
                  >
                    {fashion.name}
                  </Typography>
                  <Link to={`/fashion/${fashion.id}`}>
                    <Box
                      mt={1}
                      display="inline-flex"
                      gap={1}
                      sx={{
                        cursor: "pointer",
                        textAlign: "center",
                      }}
                      onClick={scrollToFashionDetail}
                    >
                      <Typography
                        sx={{
                          transform: "rotate(180deg)",
                          opacity: 0.8,
                        }}
                      >
                        <KeyboardBackspaceIcon
                          sx={{
                            fontSize: 30,
                            backgroundColor: "#D4D4D4",
                            borderRadius: "50px",
                            color: "#000",
                            padding: 1,
                          }}
                        />
                      </Typography>

                      <Typography
                        color="text.secondary"
                        mt={0.7}
                        variant="body6"
                        padding={1}
                      >
                        Details
                      </Typography>
                    </Box>
                  </Link>
                </CardContent>
              </Box>
            </Box>
          ))}
        </Stack>
      </Stack>
    </section>
  );
};

export default FashionDetails;
