import React, { useState, useEffect } from "react";
import {
  Divider,
  Typography,
  Box,
  Avatar,
  IconButton,
  Stack,
  CircularProgress,
  Alert,
} from "@mui/material";
import { Facebook, Twitter, Instagram } from "@mui/icons-material";
import { Link } from "react-router-dom";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { customSlick01 } from "../utils/SlickSettings";

const TopCreators = () => {
  const [creators, setCreators] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        // Fetch creators
        const responseCreators = await fetch(
          "http://localhost:8000/api/creators/"
        );
        const CreatorsData = await responseCreators.json();

        // Fetch following data
        const followingResponse = await fetch(
          "http://localhost:8000/api/creator_following/"
        );
        const followingData = await followingResponse.json();

        // Fetch review ratings data
        const reviewRatingsResponse = await fetch(
          "http://localhost:8000/api/creator-ratings/"
        );
        const reviewRatingsData = await reviewRatingsResponse.json();

        // Merge review ratings and following with creator data
        const mergedCreatorData = CreatorsData.map((creator) => {
          const creatorFollowing = followingData.filter(
            (following) => following.creator === creator.id
          );
          const creatorReviewRatings = reviewRatingsData.filter(
            (rating) => rating.creator === creator.id
          );

          return {
            ...creator,
            followers: creatorFollowing.length,
            reviewRatings: creatorReviewRatings,
          };
        });

        // Sort creators based on the number of followers
        const sortedCreators = mergedCreatorData.sort(
          (a, b) => b.followers - a.followers
        );

        // Slice the first 9 creators
        const topCreators = sortedCreators.slice(0, 9);

        if(topCreators < 1){
          setError('Nothing found')
          setLoading(false);
        } else{
          setCreators(topCreators);
          setLoading(false);
        }

        
      } catch (error) {
        console.error("Error fetching data:", error);
        setError(error.message);
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  return (
    <section style={{ overflowX: "hidden" }}>
      <Typography
        className="Heading"
        variant="h6"
        color="#000"
        textTransform="uppercase"
       
      >
        Top Fashionistas
      </Typography>
      <Divider sx={{ mt: "17px", backgroundColor: "white" }} />
      <Stack mt={3}>
        {loading ? (
          <Stack
            direction="row"
            justifyContent="center"
            alignItems="center"
            width="100%"
            mt="1rem"
          >
            <CircularProgress />
          </Stack>
        ) : error ? (
          <Alert severity="error">{error}</Alert>
        ) : (
          <Slider
            {...customSlick01}
            style={{ textAlign: "left", marginTop: "30px" }}
          >
            {creators.map((creator, index) => (
              <Link to={`/creator/${creator.id}`} key={index}>
                <Box sx={{ width: 345, maxWidth: "100%" }}>
                  <Box
                    display="flex"
                    justifyContent="center"
                    alignItems="center"
                    mt={3}
                  >
                    <Avatar
                      alt={creator.full_name}
                      src={`http://localhost:8000${creator.profile_picture}`}
                      sx={{ width: 120, height: 120 }}
                    />
                  </Box>
                  <Box px={2}>
                    <Typography variant="h6" gutterBottom color="#42a5f5">
                      {creator.full_name}
                    </Typography>

                    <Divider sx={{ mt: "12px" }} />

                    <Typography
                      variant="body6"
                      gutterBottom
                      mt={2}
                      color="gray"
                      component="div"
                      lineHeight={1.5}
                      letterSpacing={0.5}
                    >
                      {creator.description}
                    </Typography>

                    <Box
                      display="flex"
                      justifyContent="space-between"
                      alignItems="center"
                      mt={2}
                    >
                      <Box>
                        <IconButton
                          aria-label="Facebook"
                          href={creator.facebook}
                          target="_blank"
                        >
                          <Facebook />
                        </IconButton>
                        <IconButton
                          aria-label="Twitter"
                          href={creator.twitter}
                          target="_blank"
                        >
                          <Twitter />
                        </IconButton>
                        <IconButton
                          aria-label="Instagram"
                          href={creator.instagram}
                          target="_blank"
                        >
                          <Instagram />
                        </IconButton>
                      </Box>

                      <Typography
                        variant="body2"
                        color="text.secondary"
                        opacity="0.7"
                      >
                        {creator.followers} Followers
                      </Typography>
                    </Box>
                  </Box>
                </Box>
              </Link>
            ))}
          </Slider>
        )}
      </Stack>
    </section>
  );
};

export default TopCreators;
