import { Typography, Stack } from "@mui/material";
import heroImg from "../assets/imgs/heroImg.avif";

const Header = () => {
  return (
    <Stack
      sx={{
        backgroundImage: `linear-gradient(rgba(4,9,30,0.2), rgba(4,9,30,0.2)), url(${heroImg})`,
        width: "100%",
        backgroundSize: "cover",
        position: "relative",
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
        mt: "3.5%",
        padding: { lg: 35 },
      }}
      className="header"
    >
      <Stack className="HeroText">
        <Typography textTransform="capitalize" variant="h6" color="gold">
          Fashion Fusion
        </Typography>
        <Typography
          mt={3}
          lineHeight={2}
          textTransform="capitalize"
          variant="boby6"
          color="#fff"
          letterSpacing={2}
        >
          A gallery of cool and unique stuffs. From Runway to Street Style, This
          is Where Every Look Tells a Story
        </Typography>

        <button className="herobtn">
          <a href="#services" style={{ color: "#fff" }}>
            Get Started
          </a>
        </button>
      </Stack>
    </Stack>
  );
};

export default Header;
