import React, { useState } from 'react';
import { TextField, Button, Stack } from '@mui/material';

const UpdateProfileForm = ({ creator, onUpdate, token }) => {
  const [formData, setFormData] = useState({
    full_name: creator.full_name,
    contact_phone : creator.contact_phone,
    contact_email:  creator.contact_email,
    gender: creator.gender,
    date_of_birth:  creator.date_of_birth,
    description: creator.description,
    specialty: creator.specialty,
    facebook: creator.facebook,
    instagram: creator.instagram,
    whatsApp: creator.whatsApp,
    x: creator.x,
    


    
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };
  

  const handleSubmit = () => {
    fetch(`http://localhost:8000/api/creator/update/${creator.id}/`, {
      method: 'PATCH', 
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(formData),
    })
      .then((response) => response.json())
      .then((data) => {
        onUpdate(data);
      })
      .catch((error) => console.error('Error updating profile:', error));
  };

  return (
    <Stack spacing={2}>
      <TextField
        label="Full Name"
        name="full_name"
        value={formData.full_name}
        onChange={handleChange}
      />

<TextField
        label="contact_phone"
        name="contact_phone"
        value={formData.contact_phone}
        onChange={handleChange}
      />

<TextField
        label="contact_email"
        name="contact_email"
        value={formData.contact_email}
        onChange={handleChange}
      />

<TextField
        label="gender"
        name="gender"
        value={formData.gender}
        onChange={handleChange}
      />

<TextField
        label="date_of_birth"
        name="date_of_birth"
        value={formData.date_of_birth}
        onChange={handleChange}
      />
      <TextField
        label="description"
        name="description"
        value={formData.description}
        onChange={handleChange}
      />
     
      <TextField
        label="specialty"
        name="specialty"
        value={formData.specialty}
        onChange={handleChange}
      />
      <TextField
        label="facebook"
        name="facebook"
        value={formData.facebook}
        onChange={handleChange}
      />
       <TextField
        label="instagram"
        name="instagram"
        value={formData.instagram}
        onChange={handleChange}
      />
       <TextField
        label="x"
        name="x"
        value={formData.x}
        onChange={handleChange}
      />
       <TextField
        label="whatsApp"
        name="whatsApp"
        value={formData.whatsApp}
        onChange={handleChange}
      />
      
     
      <Button variant="contained" color="primary" onClick={handleSubmit}>
        Update Profile
      </Button>
    </Stack>
  );
};

export default UpdateProfileForm;
